.pizza {
    &__image {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 16px;
        max-width: 260px;
        text-align: center;
    }

    &__title {
        text-align: center;
    }

    &__characteristics {
        padding: 8px;
        margin-bottom: 16px;
        border-radius: 16px;
        background: #f3f3f3;
    }

    &__types {
        margin-bottom: 8px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
    }

    &__sizes {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 8px;
    }

    &__selector {
        &-btn {
            padding: 8px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 8px;
            background: #fff;
            box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.04);
            border: 1px solid transparent;
            color: #2c2c2c;
            font-size: 14px;
            font-weight: 700;
            letter-spacing: 0.21px;
            transition: all 0.3s ease-in-out 0s;

            &:disabled {
                opacity: 0.5;
                cursor: not-allowed;
            }

            &:not(:disabled):hover {
                border-color: #fe5f1e;
            }

            &:not(:disabled).is-active {
                background: #fe5f1e;
                color: #fff;
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 16px;

        @media(max-width: 576px){
            flex-direction: column;
            align-items: start;
        }
    }

    &__price {
        color: #2b2525;
        font-size: 22px;
        font-weight: 700;
        letter-spacing: 0.33px;

        @media(max-width: 992px){
            font-size: 20px;
        }

        @media(max-width: 768px){
            font-size: 16px;
        }
    }

    &__btn {
        border-radius: 32px;
        padding: 8px 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
        background: unset;
        border: 1px solid #fe5f1e;
        color: #fe5f1e;
        font-size: 16px;
        font-weight: 700;
        transition: all 0.3s ease-in-out 0s;

        @media(max-width: 576px){
            width: 100%;
        }

        &:not(:disabled):hover {
            background: #fe5f1e;
            color: #fff;

            & .pizza__btn-count {
                background: #fff;
                color: #fe5f1e;
            }
        }

        &:disabled{
            cursor: not-allowed;
            opacity: 0.5;
        }

        &-plus {
            margin-top: 2px;
            flex: none;
        }

        &-count {
            padding: 3px 7px;
            min-width: 22px;
            min-height: 22px;
            flex: none;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-size: 13px;
            line-height: 100%;
            border-radius: 50%;
            background: #fe5f1e;
            transition: all 0.3s ease-in-out 0s;
        }
    }
}
